body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /*background-color: #aaa;*/
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body {
  width: 980px;
  margin: 0 auto;
}

.mainLayout {
  float: left;
  width: 400px;
  height: 400px;
}
.disable {
  opacity: 0.2;
  cursor: auto;
}
.navIcon {
  padding: 66px 0 0 0;
}
.navIcon img {
  width: 60px;
  background-color: white;
  cursor: pointer;
}
.navIcon img:hover {
  background-color: #eee;
}
.items {
  display: flex;
  width: 300px;
}
.wrap {
  display: flex;
  flex-wrap: wrap;
}
.selected .topImage {
  opacity: 1;
}
.topImage {
  opacity: 0.85;
  cursor: pointer;
}
.topImage:hover {
  opacity: 1;
}

.tooltip {
  position: relative;
  display: inline-block;
}
.tooltip .tooltipText {
  visibility: hidden;
  position: absolute;
  /*left: 60px;*/
  width: 500px;
  z-index: 1;
  border-radius: 15px;
  background-color: #444444;
  color: white;
  padding: 10px;
  opacity: 0.95;
}
.tooltip:hover .tooltipText {
  visibility: visible;
}

.title {
  margin: 5px;
}
.bold {
  font-weight: bold;
}

.blueprints {
  margin: 20px;
  font-size: 0.8em;
  display: flex;
  flex-wrap: wrap;
}
.blueprint .title {
  height: 32px;
}
.blueprint {
  width: 160px;
  overflow: hidden;
  border: 0 solid black;
  /*border-bottom-width: 2px;*/
}
.blueprint:hover {
  background-color: #eee;
}
.variant {
  min-width: 100px;
}
.variant:hover {
  background-color: #ddd;
}
.selected {
  border: 2px solid red;
  border-top: 0;
  border-left: 0;
  border-right: 0;
}
.colorVariant:hover {
  border: 2px solid gray;
  opacity: 0.80;
}
.colorVariant {
  margin: 10px;
  width: 20px;
  height: 20px;
  border: 2px solid black;
  cursor: pointer;
}
.clBlack, .clSolidBlack {
  background-color: black;
}
.clNavy {
  background-color: darkblue;
}
.clWhite, .clSolidWhite {
  background-color: white;
}
.clSportGrey {
  background-color: gray;
}
.clPurple {
  background-color: purple;
}
.clForest {
  background-color: green;
}
.clRoyal, .clAthleticNavy {
  background-color: #2b2b74;
}
.clNewRed {
  background-color: red;
}
.clChocolateBrown {
  background-color: #2c2323;
}
.clMaroon {
  background-color: #9c2b2b;
}
.clOrange {
  background-color: orange;
}
.clAthleticHeather {
  background-color: #959599;
}
.clDarkGreyHeather {
  background-color: rgb(66, 66, 66);
}
.clHeatherMauve {
  background-color: #a86076;
}

.fill {
  width: 100%;
  height: 100%;
}
.sizeVariant {
  margin: 10px;
  padding: 4px 8px; 
  border: 2px solid black;
  background-color: lightgray;
  cursor: pointer;
}
.sizeVariant:hover {
  background-color: white;
}
.divImage {
  margin: 20px;
}
.images {
  display: flex;
  flex-wrap: wrap;
}

.margin20 {
  margin: 20px;
}
.selectedImage {
  /*border: 2px solid black;*/
  padding: 0 0 4px 0;
  border: 2px solid red;
  border-top: 0;
  border-left: 0;
  border-right: 0;
}
.choiceImage {
  opacity: 0.5;
  cursor: pointer;
}
.choiceImage:hover {
  opacity: 1;
}

.imgWithDetail {
  position: relative;
  display: inline-block;
}
.imgWithDetail .imgDetail {
  visibility: hidden;
  position: absolute;
  width: 500px;
  z-index: 1;
  border-radius: 15px;
  background-color: #444444;
  color: white;
  padding: 10px;
  left: 200px;
  top: -60px;
}
.imgWithDetail:hover .imgDetail {
  visibility: visible;
}